@import "tools/vendor/modular-scale";
@import "tools/vendor/media-queries";
@import "tools/libs/libs";
@import "variables/variables";

$header-height: $base-unit * 4 + $base-unit * 3;

body,
html {
  overflow-y: initial;
  -webkit-overflow-scrolling: touch;
}

body {
  min-height: 100%;
  background: linear-gradient(135deg, $bg-color-2 0%, #5352A6 100%);
}

#page-exhibition {
  text-align: center;
}

header {
  width: 100%;
  height: $header-height;
  padding: $base-unit * 1.5 $base-unit;
  color: $bg-color-1;
}

#logo {
  fill: currentColor;
  height: $header-height - $base-unit * 3;
}

main {
  width: 100%;
  height: 100%;
  max-height: calc(100% - #{$header-height});

  margin: 0 auto;
  display: flex;
  flex-flow: column;
}

.section-cviceni {
  flex: 1;

  display: flex;
  flex-flow: column;
}

.cviceni {
  flex: 1;
  position: relative;
  overflow: hidden;
  background-color: $bg-color-1;
  color: $text-color;
  font-size: ms(4);

  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;

  &:not(:last-child) {
    border-bottom: 2px solid $bg-color;
  }

  &:hover {
    // border-bottom: 0;
    font-weight: bold;
    color: $bg-color;
    background-color: transparent;

    &::before {
      opacity: .3;
    }

    span {
      &::before {
        position: absolute;
        content: "Cvičení";
        text-transform: uppercase;
        bottom: calc(100% + #{$base-unit * .5});
        left: 0;
        right: 0;
        text-align: center;
        // left: 50%;
        // transform: translateX(-50%);
        font-size: .4em;
        font-weight: normal;
      }
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    opacity: 0;
    // filter: blur(2px);
  }

  span {
    position: relative;
  }
}

#cviceni-masaryk-lany {
  &::before {
    background-position: 50% 10%;
    background-image: url("../img/tgm-hornik.jpg");
  }
}

#cviceni-skolni-obraz {
  &::before {
    background-position: 50% 35%;
    background-image: url("../img/tgm_tabor_1918.jpg");
  }
}

.section-partners {
  padding: $base-unit;
  background-color: $bg-color;

  img {
    height: $base-unit * 2;
  }
}
